.wrapper{
    padding-top: 10px;
    min-width: 300px;
    height: 100vh;
    background-color: rgb(33,33,33);
    overflow-y: auto;
}

.myProfile{
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    margin-left: 20px;
}
.name{
    margin-left: 30px;
    font-size: 36px;
}
.searchBlock{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.myNickname{
    margin-bottom: 5px;
    cursor: pointer;
}